import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Card from '../componets/Card';
import LoadingSpinner from '../componets/LoadingSpinner';
import Pagination from './Pagination';
import useToast from '../Hooks/toast';

const CardList = ({ isAdmin }) => {
    const navigate = useNavigate();
    const [posts, setPosts] = useState([]);
    const [totalPosts, setTotalPosts]= useState(1);
    const [loading, setLoading] = useState(true);
    const [currentPage, setCurrentPage] = useState(1);
    const [searchText, setSearchText] = useState('');
    const [errMessage, setErrMessage] = useState('');
 
    // Custom Hooks
    const { addToast } = useToast();

    // post  (GET)
    const getPosts = (page = 1) => {
      setCurrentPage(page);
      
      axios.get('http://localhost:3100/posts', {
        params: {
          page: page,
          search: searchText
        }
      })
      .then(res => {
        const { totalPosts, paginatedPosts, publishPost, searchPost } = res.data;
        searchText ? setPosts(searchPost) : setPosts(paginatedPosts);
        setTotalPosts(totalPosts);
        setLoading(false);
        if(!isAdmin) {
          setPosts(publishPost);
        }
        }).catch(err => {
          setErrMessage('   .');
          addToast({
            type: 'err',
            message: '  '
          });
          setLoading(false);
        })
    }
  
    useEffect(getPosts, []);

    // err 
    if(errMessage) {
      return <div className='center'>{errMessage}</div>
    }

    // post     
    const editHandler = (id) => {
      navigate(`/blog/${id}`);
    }

    const deleteHandler = (e, id) => {
      e.stopPropagation();
      window.confirm('?') &&
      axios.delete(`http://localhost:3100/posts/${id}`)
        .then(() => {
          getPosts(1);
          addToast({type: "success", message: " ."});
        }).catch(err => {
          addToast({
            type: "err",
            message: " ."
          })
        });
    } 

    // post Card rendering
    const renderList = (posts) => {
        if(loading) {
          return (
            <LoadingSpinner/>
          )
        } else if(posts.length === 0) {
          return (
            <div className='center'>   !</div>
          )
        }
        return posts.map(post => {
            return(
              <Card post={post} key={post.id} editHandler={() => editHandler(post.id)} views={post.views}>
                {isAdmin ? (
                    <button 
                        className='card_button button' 
                        onClick={(e) => deleteHandler(e, post.id)}>
                            Delete
                    </button>) : null}
              </Card>
            )
          })
        }

    // search API
    const onSearch = (e) => {
      if(e.key === 'Enter') {
        getPosts()
      }
    }

    return (
      <>
        <div className='search center'>
          <div className='search_wrap'>
            <input 
              className='search_bar'
              type='text'
              placeholder='search...'
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
              onKeyUp={onSearch}/>
            <button onClick={() => getPosts()}>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="w-5 h-5">
                <path fill-rule="evenodd" d="M9 3.5a5.5 5.5 0 100 11 5.5 5.5 0 000-11zM2 9a7 7 0 1112.452 4.391l3.328 3.329a.75.75 0 11-1.06 1.06l-3.329-3.328A7 7 0 012 9z" clip-rule="evenodd" />
              </svg>    
            </button>
          </div>
        </div>
        {renderList(posts)}
        {totalPosts > 0 && <Pagination totalPosts={totalPosts} getPosts={getPosts} currentPage={currentPage} />}
      </>
    )
}

export default CardList
